<script setup lang="ts">
import { ref, computed, onMounted } from 'vue';
import { useRoute } from 'vue-router';
import { useStore } from 'vuex';
import { Paragraph } from '@/shared/ui/text/paragraph';
import { Heading } from '@/shared/ui/text/heading';
import { Button } from '@/shared/ui/button';

const route = useRoute();
const store = useStore();

// Реактивные данные
const currentProduct = ref(null);
const selectedPrice = ref('');
const quantity = ref(1);

// Получаем продукт по slug из store
const product = computed(() => {
  const slug = route.params.slug as string;
  return store.getters['products/getProductBySlug'](slug);
});

// Загружаем продукты если их нет в store
onMounted(async () => {
  if (!product.value) {
    await store.dispatch('products/fetchProducts', { limit: 50, offset: 0 });
  }
});

// Обработчики
const handleAddToCart = () => {
  if (product.value) {
    // Логика добавления в корзину
    console.log('Adding to cart:', {
      product: product.value,
      quantity: quantity.value,
      selectedPrice: product.value
    });
  }
};

const incrementQuantity = () => {
  quantity.value += 1;
};

const decrementQuantity = () => {
  if (quantity.value > 1) {
    quantity.value -= 1;
  }
};
</script>

<template>
  <section class="product-content" v-if="product">

    <div class="content-left">
      <Heading tagName="h1" color="dark" size="l" class="title">
        {{ product.name }}
      </Heading>
      <img :src="product.photo || '/placeholder-image.jpg'" :alt="product.name" class="image">
    </div>

    <div class="content-right">
      <Heading tagName="h1" color="dark" size="l" class="title">
        {{ product.name }}
      </Heading>
      <Paragraph tagName="span" color="dark" size="xxl" class="total-cost">
        {{ (selectedPrice || product.price) * quantity }} Br
      </Paragraph>

      <div class="cost">
        <select v-model="selectedPrice" v-if="product.prices && product.prices.length">
          <option v-for="price in product.prices" :key="price.id" :value="price.value">
            {{ price.name }} - {{ price.value }} Br
          </option>
        </select>

        <div class="counter">
          <div class="buttons">
            <button @click="decrementQuantity">-</button>
            <input type="text" v-model="quantity" readonly>
            <button @click="incrementQuantity">+</button>
          </div>
          <Button tagName="button" size="xs" color="white" @click="handleAddToCart" class="add-button">
            Добавить в корзину
          </Button>
        </div>

      </div>
      <Paragraph tagName="span" color="dark" size="xxl" class="total-cost-mobile">
              {{ (selectedPrice || product.price) * quantity }} Br
            </Paragraph>

      <article class="description" v-if="product.description">
        <Paragraph tagName="p" color="dark" size="l">
          {{ product.description }}
        </Paragraph>
      </article>

      <article class="description" v-else>
        <Paragraph tagName="p" color="dark" size="l">
          {{ product.name }} - Приобрести в магазине Top Cvetok
        </Paragraph>

      </article>
    </div>
  </section>

  <section v-else-if="!product && store.state.products.productsData.length">
    <Paragraph tagName="p" color="dark" size="l">
      Продукт не найден
    </Paragraph>
  </section>

  <section v-else>
    <Paragraph tagName="p" color="dark" size="l">
      Загрузка...
    </Paragraph>
  </section>
</template>




<style lang="scss" scoped>
.product-content {
  display: flex;
  gap: 50px;
  width: 100%;


  .content-left {
    display: block;
    width: 40%;
    height: auto;

    .title {
      display: none;
    }

    img {
      display: block;
      width: 100%;
      height: auto;
      border-radius: 30px;
    }
  }

  .content-right {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 0 0 40px;

    .title {
      margin-bottom: 30px;
    }

    .cost {
      display: flex;
      gap: 20px;
      width: 100%;
    }

    .counter {
      display: flex;
      gap: 20px;

      .buttons {
        display: flex;
        gap: 10px;
        align-items: center;

        .total-cost-mobile {
          display: none;
        }

        button {
          width: 40px;
          height: 40px;
          border-radius: 40px;
          border: none;
          font-size: 24px;
          vertical-align: center;
          background-color: white;
          border: 1px solid rgb(177, 181, 195);

          &:hover {
            background-color: #6A6E49;
          }

        }

        input {
          border: none;
          text-align: center;
          width: 50px;
          padding: 0 10px;
          font-size: 24px;
          vertical-align: center;
        }
      }
    }

    .button {
      height: 40px;
    }

    .description {}
  }
}

@media screen and (max-width: 541px) {
  .product-content {
    flex-direction: column;
    gap: 30px;

    .content-left {
      width: 100%;

      .title {
        display: block;
        margin-bottom: 30px;
      }
    }

    .content-right {
      .title {
        display: none;
      }
      .total-cost{
        display: none;
      }

      .cost {
        width: 100%;

        .counter {
          flex-direction: column;
          width: 100%;
          align-items: center;

          .total-cost-mobile {
            display: block;
          }

          .add-button {
            min-width: 100%;
          }
        }
      }
    }
  }

}</style>
